@media (max-width: 1199px) {
  .projects {
    padding: 40px;
  }
}
@media (max-width: 767px) {
  .projects {
    padding: 20px 15px;
  }
}

.projects__title {
  margin: 0;
  padding: 0;
  font-size: 35px;
  font-weight: 700;
  line-height: 28px;
  text-align: center;
  color: #43258e;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1199px) {
  .projects__title {
    font-size: 32px;
  }
}

.projects__list {
  margin: 40px 0 0;
  padding: 0;
}
@media (max-width: 1199px) {
  .projects__list {
    margin: 30px 0 0;
  }
}
@media (max-width: 767px) {
  .projects__list {
    margin: 20px 0 0;
  }
}

.projects__item {
  margin: 0 0 60px;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 60px;
  list-style-type: none;
}
@media (max-width: 1199px) {
  .projects__item {
    display: flex;
    flex-direction: column;
    gap: 0;
    margin-bottom: 80px;
  }
  .projects__item:last-child {
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .projects__item {
    margin-bottom: 50px;
  }
  .projects__item:last-child {
    margin-bottom: 20px;
  }
}

.projects__img {
  min-height: 370px;
  object-fit: cover;
}
@media (max-width: 1199px) {
  .projects__img {
    width: 100%;
    height: 550px;
    margin-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .projects__img {
    height: 100%;
  }
}

.projects__link {
  margin: 0 0 0 -12px;
  padding: 10px;
  font-size: 28px;
  font-weight: 700;
  line-height: 28px;
  color: #334482;
  text-decoration: underline;
}
.projects__link:hover {
  opacity: 0.6;
}
.projects__link:active {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .projects__link {
    font-size: 23px;
    margin: 0;
  }
  .projects__link span {
    display: none;
  }
}

.description {
  margin: 15px 0 0;
  padding: 0;
}
@media (max-width: 1199px) {
  .description {
    margin-left: 18px;
  }
}

.description__item {
  margin: 5px 0 0;
  padding: 0;
  font-weight: 500;
  font-size: 16px;
  list-style-type: disc;
}
@media (max-width: 450px) {
  .description__item {
    font-size: 14px;
  }
}

.info {
  margin: 0 0 0 20px;
  padding: 0;
}

.info__item {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 450px) {
  .info__item {
    font-size: 14px;
  }
}

